@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@400&display=swap');

.browser {
  background-color: #D6EEF2;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  width: 800px;
  height: 600px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: relative;
}

.title-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 30px;
  background-color: #8d8d8d;
  border:  2px solid #5f5f5f;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding: 0 10px;
}

.buttons {
  display: flex;
  align-items: center;
}

.close-button,
.minimize-button {
  width: 12px;
  height: 12px;
  margin: 0 4px;
  border-radius: 50%;
}

.close-button {
  background-color: #fb5151;
}

.minimize-button {
  background-color: #f4c35a;
}

.tabs {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 30px;
  padding-left: 2px;
}

.tab {
  padding: 0 10px;
  line-height: 30px;
  font-size: 14px;
  cursor: pointer;
  font-family: 'Fira Code', monospace;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  text-decoration: none; 
  color: inherit;
  border-right: 2px solid #5f5f5f;
  margin-right: 5px;
}

.tab:hover {
  background-color: #E5E5E5;
}

.tab.active {
  background-color: #FFF;
  font-weight: bold;
}

.content {
  background-color: #ededda;
  height: calc(100% - 60px);
  padding: 20px;
  overflow-y: auto;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  border-top: none;
}

/* Custom scrollbar styles */
.content::-webkit-scrollbar {
  width: 8px;
  background-color: #d9d9c2;; /* Match the background color of the screen */
}

.content::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.4);
  transition: background-color 0.3s ease;
}

.content::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.6); /* Lighten up when hovered */
}
